.header {
  background: #f1f3f5;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.2); }
  .header .header-content {
    height: 5rem;
    width: 1400px;
    margin: 0 auto;
    padding-left: 10%;
    padding-right: 10%;
    display: flex;
    align-items: center; }
    @media (max-width: 1399px) {
      .header .header-content {
        width: 100%; } }
    @media (max-width: 767px) {
      .header .header-content {
        display: block; } }
    .header .header-content .brand {
      color: black;
      font-size: 1.5rem;
      font-weight: 600; }
    .header .header-content .right {
      margin-left: auto; }
