.user-detail {
  margin: 0 auto;
  width: 1400px;
  padding-top: 20px;
  padding-left: 10%;
  min-height: 60px; }
  @media (max-width: 767px) {
    .user-detail {
      display: block; } }
  .user-detail .user-profile {
    display: flex;
    margin-bottom: 5px; }
    @media (max-width: 767px) {
      .user-detail .user-profile {
        display: block; } }
    .user-detail .user-profile .avatar img {
      border-radius: 0.5rem;
      width: 230px;
      height: 230px; }
    .user-detail .user-profile .profile-info {
      padding-left: 2%;
      flex-direction: column;
      min-height: 60px; }
      @media (max-width: 767px) {
        .user-detail .user-profile .profile-info {
          padding-left: 0; } }
      .user-detail .user-profile .profile-info .title {
        display: flex;
        flex-wrap: wrap;
        align-items: baseline;
        margin-bottom: 5px; }
        @media (max-width: 767px) {
          .user-detail .user-profile .profile-info .title {
            padding-top: 20px; } }
        .user-detail .user-profile .profile-info .title .user-id {
          margin-left: 10px;
          color: #4263eb; }
      .user-detail .user-profile .profile-info .bio {
        min-height: 40px; }
        .user-detail .user-profile .profile-info .bio p {
          font-size: 15px; }
      .user-detail .user-profile .profile-info .stats {
        padding-top: 1px; }
        .user-detail .user-profile .profile-info .stats .stats-itemList {
          display: flex;
          list-style: none;
          padding-left: 2%;
          margin: auto; }
          @media (max-width: 767px) {
            .user-detail .user-profile .profile-info .stats .stats-itemList {
              padding-left: 0; } }
          .user-detail .user-profile .profile-info .stats .stats-itemList .stats-item {
            margin-right: 15px; }
            .user-detail .user-profile .profile-info .stats .stats-itemList .stats-item .stats-label {
              font-size: 15px; }
            .user-detail .user-profile .profile-info .stats .stats-itemList .stats-item .stats-value {
              font-size: 25px;
              text-align: center; }
  .user-detail h3,
  .user-detail p {
    margin: 0; }
