.followers {
  width: 50%; }
  @media (max-width: 1399px) {
    .followers {
      width: 100%; } }
  .followers .followers-list {
    list-style: none;
    margin: 0;
    padding: 0; }
    .followers .followers-list .follower {
      border-bottom: 1px solid #ddd;
      margin-bottom: 10px;
      padding-bottom: 20px; }
      .followers .followers-list .follower .avatar {
        display: inline; }
      .followers .followers-list .follower .user-name {
        display: inline;
        font-size: 1.2rem;
        margin-left: 10px; }
