.user-list {
  margin: 0 auto;
  max-width: 1200px; }
  .user-list .user-ul {
    margin: 0;
    list-style: none; }
    @media (min-width: 769px) {
      .user-list .user-ul {
        padding-left: 8%;
        display: flex;
        flex-wrap: wrap; } }
    @media (max-width: 767px) {
      .user-list .user-ul {
        padding-left: 40%; } }
  .user-list .user-li {
    margin-bottom: 10px;
    flex-basis: calc(100% / 3); }
  .user-list .avatar {
    display: inline; }
  .user-list .user-name {
    display: inline;
    font-size: 1.2rem;
    margin-left: 10px; }
