@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);
@import url(//cdn.jsdelivr.net/gh/velopert/font-d2coding@1.2.1/d2coding.css);
.form {
  position: relative; }
  .form .form-input {
    width: 100%;
    padding: 8px;
    padding-right: 60px;
    box-shadow: inset 0 2px 2px rgba(0, 0, 0, 0.1);
    border: 1px solid #bbb; }
  .form .form-btn {
    background: transparent;
    position: absolute;
    right: 0px; }

.header {
  background: #f1f3f5;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.2); }
  .header .header-content {
    height: 5rem;
    width: 1400px;
    margin: 0 auto;
    padding-left: 10%;
    padding-right: 10%;
    display: flex;
    align-items: center; }
    @media (max-width: 1399px) {
      .header .header-content {
        width: 100%; } }
    @media (max-width: 767px) {
      .header .header-content {
        display: block; } }
    .header .header-content .brand {
      color: black;
      font-size: 1.5rem;
      font-weight: 600; }
    .header .header-content .right {
      margin-left: auto; }

.result {
  margin: auto;
  width: 100%;
  padding: 10px; }
  .result .message {
    text-align: center;
    padding: 6px; }

.user-list {
  margin: 0 auto;
  max-width: 1200px; }
  .user-list .user-ul {
    margin: 0;
    list-style: none; }
    @media (min-width: 769px) {
      .user-list .user-ul {
        padding-left: 8%;
        display: flex;
        flex-wrap: wrap; } }
    @media (max-width: 767px) {
      .user-list .user-ul {
        padding-left: 40%; } }
  .user-list .user-li {
    margin-bottom: 10px;
    flex-basis: calc(100% / 3); }
  .user-list .avatar {
    display: inline; }
  .user-list .user-name {
    display: inline;
    font-size: 1.2rem;
    margin-left: 10px; }

.pagination {
  display: flex;
  justify-content: center; }
  .pagination .link {
    font-size: 20px;
    border-radius: 0.25rem;
    border: 1px solid #dee2e6;
    padding: 0.5rem 0.5rem;
    margin-top: 20px;
    margin-bottom: 50px; }
  .pagination .link:hover {
    color: aquamarine; }

.footer {
  background: #f1f3f5;
  position: fixed;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.2);
  bottom: 0;
  left: 0;
  right: 0;
  height: 30px; }
  .footer p {
    text-align: center;
    margin: 0; }
  .footer a {
    color: #4263eb;
    font-size: 20px;
    font-weight: 600; }

.profile-info {
  padding-left: 0; }
  .profile-info .item-list {
    padding-left: 0; }
  .profile-info .item {
    display: flex;
    margin-bottom: 5px; }
    .profile-info .item .text {
      margin-left: 10px;
      font-size: 14px; }
    .profile-info .item .link {
      color: #4263eb; }

.user-detail {
  margin: 0 auto;
  width: 1400px;
  padding-top: 20px;
  padding-left: 10%;
  min-height: 60px; }
  @media (max-width: 767px) {
    .user-detail {
      display: block; } }
  .user-detail .user-profile {
    display: flex;
    margin-bottom: 5px; }
    @media (max-width: 767px) {
      .user-detail .user-profile {
        display: block; } }
    .user-detail .user-profile .avatar img {
      border-radius: 0.5rem;
      width: 230px;
      height: 230px; }
    .user-detail .user-profile .profile-info {
      padding-left: 2%;
      flex-direction: column;
      min-height: 60px; }
      @media (max-width: 767px) {
        .user-detail .user-profile .profile-info {
          padding-left: 0; } }
      .user-detail .user-profile .profile-info .title {
        display: flex;
        flex-wrap: wrap;
        align-items: baseline;
        margin-bottom: 5px; }
        @media (max-width: 767px) {
          .user-detail .user-profile .profile-info .title {
            padding-top: 20px; } }
        .user-detail .user-profile .profile-info .title .user-id {
          margin-left: 10px;
          color: #4263eb; }
      .user-detail .user-profile .profile-info .bio {
        min-height: 40px; }
        .user-detail .user-profile .profile-info .bio p {
          font-size: 15px; }
      .user-detail .user-profile .profile-info .stats {
        padding-top: 1px; }
        .user-detail .user-profile .profile-info .stats .stats-itemList {
          display: flex;
          list-style: none;
          padding-left: 2%;
          margin: auto; }
          @media (max-width: 767px) {
            .user-detail .user-profile .profile-info .stats .stats-itemList {
              padding-left: 0; } }
          .user-detail .user-profile .profile-info .stats .stats-itemList .stats-item {
            margin-right: 15px; }
            .user-detail .user-profile .profile-info .stats .stats-itemList .stats-item .stats-label {
              font-size: 15px; }
            .user-detail .user-profile .profile-info .stats .stats-itemList .stats-item .stats-value {
              font-size: 25px;
              text-align: center; }
  .user-detail h3,
  .user-detail p {
    margin: 0; }

.repositoreis {
  width: 50%;
  padding-right: 20px; }
  @media (max-width: 1399px) {
    .repositoreis {
      width: 100%; } }
  .repositoreis .repos-list {
    list-style: none;
    margin: 0;
    padding: 0; }
    .repositoreis .repos-list .repos {
      border-bottom: 1px solid #ddd;
      margin-bottom: 20px;
      padding-bottom: 10px; }
      .repositoreis .repos-list .repos .repos-title {
        font-size: 20px; }
        .repositoreis .repos-list .repos .repos-title .repos-link {
          color: #4263eb; }
        .repositoreis .repos-list .repos .repos-title .repos-fork {
          font-size: 15px;
          margin-left: 5px; }
      .repositoreis .repos-list .repos .repos-description {
        font-size: 15px; }
      .repositoreis .repos-list .repos .repos-footer {
        display: flex;
        font-size: 12px; }
        .repositoreis .repos-list .repos .repos-footer .pushDate {
          margin-left: auto; }
        .repositoreis .repos-list .repos .repos-footer p {
          margin-right: 10px; }

.followers {
  width: 50%; }
  @media (max-width: 1399px) {
    .followers {
      width: 100%; } }
  .followers .followers-list {
    list-style: none;
    margin: 0;
    padding: 0; }
    .followers .followers-list .follower {
      border-bottom: 1px solid #ddd;
      margin-bottom: 10px;
      padding-bottom: 20px; }
      .followers .followers-list .follower .avatar {
        display: inline; }
      .followers .followers-list .follower .user-name {
        display: inline;
        font-size: 1.2rem;
        margin-left: 10px; }

.chart {
  margin-right: 30px; }

body {
  margin: 0;
  box-sizing: border-box;
  font-family: "Noto Sans KR", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

* {
  box-sizing: inherit; }

a {
  text-decoration: inherit;
  color: inherit; }

a:hover {
  color: aquamarine; }

